.mock-draft {
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  padding: 20px 0;
}

.draft-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
  position: relative;
}

.draft-info {
  text-align: center;
  flex: 1;
}

.draft-info h1 {
  font-size: 2.5rem;
  margin-bottom: 15px;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.turn-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: center;
}

.turn-label {
  color: #94a3b8;
  font-size: 1.1rem;
}

.turn-team {
  background: rgba(59, 130, 246, 0.2);
  padding: 8px 16px;
  border-radius: 20px;
  font-weight: 600;
  color: #3b82f6;
  border: 2px solid rgba(59, 130, 246, 0.3);
}

.turn-team.user-turn {
  background: #3b82f6;
  color: white;
  border-color: #3b82f6;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.back-btn {
  position: absolute;
  left: 0;
}

.draft-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  margin-bottom: 40px;
}

.draft-board {
  padding: 30px;
}

.draft-board h2 {
  margin-bottom: 25px;
  color: #e2e8f0;
}

.draft-rounds {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.draft-round h3 {
  color: #94a3b8;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

.round-picks {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 15px;
}

.pick-slot {
  background: rgba(15, 23, 42, 0.6);
  border: 2px solid rgba(59, 130, 246, 0.2);
  border-radius: 8px;
  padding: 15px;
  text-align: center;
  transition: all 0.3s ease;
}

.pick-slot:hover {
  border-color: rgba(59, 130, 246, 0.5);
  background: rgba(15, 23, 42, 0.8);
}

.team-label {
  font-size: 0.8rem;
  color: #64748b;
  margin-bottom: 8px;
  font-weight: 600;
}

.empty-slot {
  color: #475569;
  font-size: 1.2rem;
  font-weight: 300;
}

.picked-player {
  text-align: center;
}

.picked-player .player-name {
  font-weight: 600;
  color: #e2e8f0;
  margin-bottom: 5px;
  font-size: 0.9rem;
}

.picked-player .player-details {
  font-size: 0.8rem;
  color: #94a3b8;
}

.player-selection {
  padding: 30px;
}

.player-selection h2 {
  margin-bottom: 25px;
  color: #e2e8f0;
}

.filters {
  display: flex;
  gap: 15px;
  margin-bottom: 25px;
  flex-wrap: wrap;
}

.player-list {
  max-height: 500px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.player-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(59, 130, 246, 0.2);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.player-card:hover {
  background: rgba(15, 23, 42, 0.8);
  border-color: rgba(59, 130, 246, 0.4);
}

.player-info {
  flex: 1;
}

.player-name {
  font-weight: 600;
  color: #e2e8f0;
  margin-bottom: 5px;
}

.player-details {
  font-size: 0.9rem;
  color: #94a3b8;
}

.draft-btn {
  padding: 8px 16px;
  font-size: 14px;
}

@media (max-width: 1024px) {
  .draft-content {
    grid-template-columns: 1fr;
  }
  
  .draft-header {
    flex-direction: column;
    gap: 20px;
    text-align: center;
  }
  
  .back-btn {
    position: static;
    align-self: flex-start;
  }
}

@media (max-width: 768px) {
  .draft-info h1 {
    font-size: 2rem;
  }
  
  .filters {
    flex-direction: column;
  }
  
  .round-picks {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }
  
  .player-card {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
}
