* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
  background: linear-gradient(135deg, #1e3c72 0%, #2a5298 100%);
  min-height: 100vh;
  color: #333;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* Button Styles */
.btn {
  padding: 12px 24px;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.4);
}

.btn-primary:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.6);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: rgba(255, 255, 255, 0.3);
}

/* Welcome Screen */
.welcome-screen {
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  filter: brightness(0.7);
}

.welcome-content {
  background: rgba(0, 0, 0, 0.6);
  padding: 40px;
  border-radius: 20px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.start-btn {
  font-size: 20px;
  padding: 16px 32px;
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a24 100%);
  border: none;
  border-radius: 12px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(255, 107, 107, 0.4);
}

.start-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 35px rgba(255, 107, 107, 0.6);
}

/* Player Hub */
.player-hub {
  min-height: 100vh;
  padding: 20px 0;
}

.hub-header {
  text-align: center;
  margin-bottom: 40px;
  color: white;
  position: relative;
}

.hub-title {
  font-size: 3rem;
  margin-bottom: 10px;
  background: linear-gradient(135deg, #ff6b6b, #feca57);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hub-subtitle {
  font-size: 1.2rem;
  opacity: 0.8;
}

.back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
}

.search-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 20px;
  margin-bottom: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.search-section h2 {
  margin-bottom: 20px;
  color: #333;
  font-size: 1.5rem;
}

.search-filters {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.form-input, .form-select {
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
  flex: 1;
  min-width: 200px;
}

.form-input:focus, .form-select:focus {
  outline: none;
  border-color: #667eea;
}

.results-count {
  color: #666;
  font-size: 14px;
}

.players-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.player-card {
  background: rgba(255, 255, 255, 0.95);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.player-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.15);
}

.player-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.player-name {
  font-weight: bold;
  font-size: 1.1rem;
  color: #333;
}

.player-position {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: bold;
}

.player-team {
  color: #666;
  font-size: 14px;
  margin-bottom: 10px;
}

.player-id {
  color: #999;
  font-size: 12px;
}

.player-details {
  display: flex;
  gap: 15px;
  margin-bottom: 10px;
  font-size: 12px;
}

.player-exp {
  background: linear-gradient(135deg, #ff6b6b, #feca57);
  color: white;
  padding: 2px 8px;
  border-radius: 12px;
  font-weight: bold;
}

.player-college {
  color: #666;
  font-style: italic;
}

/* Pagination Styles */
.pagination-section {
  background: rgba(255, 255, 255, 0.95);
  padding: 25px;
  border-radius: 20px;
  margin-bottom: 30px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.pagination-info {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
  font-weight: 600;
}

.pagination-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  flex-wrap: wrap;
}

.page-numbers {
  display: flex;
  gap: 8px;
  align-items: center;
}

.page-btn {
  padding: 8px 12px;
  min-width: 40px;
  font-size: 14px;
}

.page-btn.active {
  background: linear-gradient(135deg, #667eea, #764ba2);
  color: white;
  border-color: transparent;
}

.page-dots {
  color: #666;
  font-weight: bold;
  padding: 0 8px;
}

.action-section {
  text-align: center;
}

.start-draft-btn {
  font-size: 18px;
  padding: 16px 32px;
}

/* Glass Effect */
.glass {
  background: rgba(255, 255, 255, 0.25);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.18);
}

/* Mock Draft */
.mock-draft {
  min-height: 100vh;
  padding: 20px 0;
}

.draft-header {
  text-align: center;
  margin-bottom: 40px;
  color: white;
  position: relative;
}

.draft-info h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  background: linear-gradient(135deg, #ff6b6b, #feca57);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.turn-indicator {
  background: rgba(255, 255, 255, 0.1);
  padding: 15px 25px;
  border-radius: 25px;
  display: inline-block;
  backdrop-filter: blur(10px);
}

.turn-label {
  margin-right: 10px;
  opacity: 0.8;
}

.turn-team {
  font-weight: bold;
  color: #feca57;
}

.draft-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.draft-board, .player-selection {
  background: rgba(255, 255, 255, 0.95);
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}

.draft-board h2, .player-selection h2 {
  margin-bottom: 20px;
  color: #333;
  font-size: 1.5rem;
}

.draft-round h3 {
  color: #666;
  margin-bottom: 15px;
  font-size: 1.1rem;
}

.round-picks {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 10px;
  margin-bottom: 20px;
}

.pick-slot {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 10px;
  text-align: center;
  border: 2px dashed #dee2e6;
}

.team-label {
  font-size: 12px;
  color: #666;
  margin-bottom: 5px;
}

.empty-slot {
  color: #999;
  font-size: 18px;
}

.filters {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.player-list {
  max-height: 500px;
  overflow-y: auto;
}

.player-list .player-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  padding: 15px;
}

.player-info {
  flex: 1;
}

.player-details {
  color: #666;
  font-size: 14px;
  margin-top: 5px;
}

.draft-btn {
  padding: 8px 16px;
  font-size: 14px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .draft-content {
    grid-template-columns: 1fr;
  }
  
  .search-filters {
    flex-direction: column;
  }
  
  .players-grid {
    grid-template-columns: 1fr;
  }
  
  .hub-title {
    font-size: 2rem;
  }
  
  .draft-info h1 {
    font-size: 2rem;
  }
}
