.player-hub {
  min-height: 100vh;
  background: linear-gradient(135deg, #0f172a 0%, #1e293b 100%);
  padding: 20px 0;
}

.hub-header {
  text-align: center;
  margin-bottom: 40px;
}

.hub-title {
  font-size: 3rem;
  margin-bottom: 10px;
  background: linear-gradient(135deg, #3b82f6, #8b5cf6);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.hub-subtitle {
  font-size: 1.2rem;
  color: #94a3b8;
  margin-bottom: 20px;
}

.back-btn {
  position: absolute;
  top: 20px;
  left: 20px;
}

.search-section {
  padding: 30px;
  margin-bottom: 30px;
}

.search-section h2 {
  margin-bottom: 20px;
  color: #e2e8f0;
}

.search-filters {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
  flex-wrap: wrap;
}

.form-input,
.form-select {
  padding: 12px 16px;
  border: 2px solid rgba(59, 130, 246, 0.3);
  border-radius: 8px;
  background: rgba(15, 23, 42, 0.8);
  color: white;
  font-size: 16px;
  transition: all 0.3s ease;
}

.form-input:focus,
.form-select:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-input {
  flex: 1;
  min-width: 200px;
}

.form-select {
  min-width: 150px;
}

.results-count {
  color: #94a3b8;
  font-size: 14px;
}

.players-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
}

.player-card {
  padding: 20px;
  transition: all 0.3s ease;
}

.player-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(59, 130, 246, 0.2);
}

.player-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.player-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #e2e8f0;
}

.player-position {
  background: #3b82f6;
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.8rem;
  font-weight: 600;
}

.player-team {
  color: #94a3b8;
  margin-bottom: 10px;
}

.player-id {
  color: #64748b;
  font-size: 0.9rem;
}

.action-section {
  text-align: center;
}

.start-draft-btn {
  font-size: 18px;
  padding: 15px 30px;
}

@media (max-width: 768px) {
  .hub-title {
    font-size: 2rem;
  }
  
  .search-filters {
    flex-direction: column;
  }
  
  .form-input,
  .form-select {
    min-width: 100%;
  }
  
  .players-grid {
    grid-template-columns: 1fr;
  }
}
